.footer {
  background: $second-bg-color;
  margin: 0 auto;
}

.footer__content {
  padding-top: 57px;
  @media screen and (min-width: 768px) {
    padding-top: 85px;
    display: flex;
    justify-content: space-between;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 85px;
    display: flex;
    justify-content: space-between;
  }
}

.footer__title {
  font-family: Titan One;
  text-transform: uppercase;
  font-weight: normal;
  animation: fadein 2s;
  @keyframes fadein {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  @media screen and (min-width: 768px) {
    width: 340px;
    padding-bottom: 91px;
    margin-right: 40px;
  }

  @media screen and (min-width: 1280px) {
    width: 480px;
    padding-bottom: 97px;
    margin-right: 129px;
  }
}

.footer__title-main {
  font-family: Titan One;
  font-size: 18px;
  line-height: 1.16;
  margin-bottom: 17px;
  width: 232px;
  color: $second-hero-bg-color;
  
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    margin-top: 7px;
    margin-bottom: 16px;
    width: auto;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
    letter-spacing: 0.04em;
    margin-bottom: 15px;
    margin-top: 0;
  }
}

.footer__title-second {
  margin-bottom: 43px;
  color: $text-title-color;
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 39px;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    letter-spacing: 0.06em;
    width: auto;
  }

  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.14;
  }
}

.soc-list__item {
  margin-left: 28px;
  &:first-child {
    margin-left: 0px;
  }
}



.soc-list {
  margin-top: 0px;
  margin-bottom: 46px;
  padding: 0px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
}

.soc-list__link {
  display: flex;
  height: 34px;
  width: 34px;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  background-color: $socials-bg-color;
  // margin-left: 28px;
    display: flex;
    height: 34px;
    width: 34px;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    background-color: $socials-bg-color;
    // margin-left: 28px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

.soc-list__link:hover,
.soc-list__link:focus {
  background-color: $text-title-color;
}

.footer__contact-connect {
  display: flex;
  justify-content: flex-end;
  font-family: DM Sans;
  font-weight: 700;
  font-size: 12px;
  line-height: 22px;
  text-align: right;
  color: $main-text-color;
  margin-bottom: 43px;

  @media screen and (min-width: 768px) {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.9;
  }
}

.footer__contact-tel {
  text-decoration: none;
  margin-left: 4px;
  color: $text-title-color;
}

.copy-text {
  font-family: DM Sans;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  color: $second-text-color;
  padding: 22px 91px 25px 79px;
  margin: 0;
  border-top: 1px solid #e1e1e1;

  @media screen and (min-width: 768px) {
    line-height: 1.9;
    letter-spacing: 0.02em;
    padding: 29px 245px 18px 245px;
  }

  @media screen and (min-width: 1280px) {
    line-height: 1.9;
    letter-spacing: 0.02em;
  }
}
