.reviews {
  padding-top: 60px;
  padding-bottom: 68px;
  @media screen and (min-width: 768px) {
    padding-top: 121px;
    padding-bottom: 119px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 123px;
    padding-bottom: 132px;
  }
}

.reviews__slide {
  margin-bottom: 30px;
  @media screen and (min-width: 768px) {
    margin-bottom: 35px;
  }
}
.reviews__img {
  width: 85px;
  height: 85px;
  border-radius: 50%;
  object-fit: cover;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 29px;
  @media screen and (min-width: 768px) {
    margin-bottom: 19px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 15px;
  }
}
.reviews__thumb {
  position: relative;

  text-align: center;
  margin-bottom: 30px;
  padding: 43px 66px 26px 57px;
  background-color: #fafafa;
  @media screen and (min-width: 768px) {
    padding: 47px 80px 34px;
    margin-bottom: 24px;
  }
  @media screen and (min-width: 1280px) {
    padding: 47px 89px 39px 87px;
    margin-bottom: 15px;
  }

  &::before {
    content: '';
    display: block;
    width: 54px;
    height: 40px;
    margin: 0;
    position: absolute;
    top: 28px;
    left: 61px;
    background-image: url('../images/review.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    @media screen and (min-width: 768px) {
      top: 30px;
      left: 40px;
    }
    @media screen and (min-width: 1280px) {
      top: 32px;
      left: 50px;
    }
  }
}
.reviews__comment {
  @include font(normal, 500, 12px, 2.16, 0.04em);
  position: relative;
  display: inline;
  padding-bottom: 6px;
  color: $main-text-color;
  background-image: repeating-linear-gradient(#e1e1e1, #e1e1e1);
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: left bottom;
  @media screen and (min-width: 768px) {
    @include font(normal, 500, 14px, 2.55, 0.04em);
  }
  @media screen and (min-width: 1280px) {
    @include font(normal, 500, 16px, 1.86, 0.04em);
    padding-bottom: 7px;
  }
}
.reviews__deco {
  position: relative;
  display: block;
  margin-bottom: 30px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: $button-second-color;
  margin-right: auto;
  margin-left: auto;
  @media screen and (min-width: 768px) {
    margin-bottom: 14px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 15px;
  }
  &::before,
  &::after {
    position: absolute;
    top: 0;

    content: '';
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: $button-second-color;
  }
  &::before {
    left: -10px;
  }
  &::after {
    left: 10px;
  }
}
.reviews__subtitle {
  @include font(normal, 500, 14px, 1.86, 0.03em);

  text-align: center;
  color: $main-text-color;
  @media screen and (min-width: 768px) {
    @include font(normal, 500, 16px, 1.86, 0.03em);
  }
  @media screen and (min-width: 1280px) {
    @include font(normal, 500, 20px, 1.86, 0.03em);
  }
}
.reviews__slide-list {
  @include flex();
}
.reviews__slide-item {
  border-radius: 50%;
  width: 12px;
  height: 12px;
  background-color: $comment-second-color;
  &:not(:last-child) {
    margin-right: 18px;
  }
  &--current {
    @include flex();
    border-radius: 0;
    background-color: transparent;
  }
}
